.main_btn {
  width: 75px;
  height: 25px;
  font-size: 26px;
  color: #fff;
  border: 2px white solid;
  border-radius: 10px;
  text-align: center;
}

.main_btn:hover {
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #0fa,
    0 0 82px #0fa, 0 0 92px #0fa, 0 0 102px #0fa, 0 0 151px #0fa;
  cursor: pointer;
}
